<template>
	<div id="app">
		<main>
			<div id="left">
				<div class="main-text">
					<img src="static/img/desk.svg" alt="http://lukaszadam.com" id="desk">
					<h1>{{h1}}</h1>
					<h2>{{h2}}</h2>
					<ul id="social">
						<li v-for="social in socials">
							<a :href=social.url target="_blank"><i class="fa" v-bind:class="social.icon"></i></a>
						</li>
					</ul>
				</div>
			</div>
			<router-view/>
		</main>
	</div>
</template>

<script>
	export default {
		name: 'App',
		data() {
			return {
				hamburgerOpen: false,
				h1: 'Hey! I am Sadman,',
				h2: 'a front-end developer based in Dhaka, Bangladesh.',
				socials: [
					{
						url: 'mailto:hello@sadmansh.com',
						icon: 'fa-envelope'
					},
					{
						url: 'https://github.com/sadmansh',
						icon: 'fa-github'
					},
					{
						url: 'https://instagram.com/sadmansh',
						icon: 'fa-instagram'
					},
					{
						url: 'https://twitter.com/sadmansh',
						icon: 'fa-twitter'
					}
				]
			}
		},
		methods: {
			toggleHamburger: function() {
				this.hamburgerOpen = !this.hamburgerOpen;
			}
		}
	}
</script>

<style lang="sass">

@import "./styles/base.scss"

*,
*::before,
*::after 
	box-sizing: border-box

body 
	margin: 0

#app
	font-family: 'Avenir', Helvetica, Arial, sans-serif
	-webkit-font-smoothing: antialiased
	-moz-osx-font-smoothing: grayscale
	color: #2c3e50

#hamburger
	position: relative
	// top: 2rem
	// left: 2rem
	// display: none
	width: 30px
	height: 29px
	cursor: pointer

	.bar
		position: absolute
		top: 12px
		height: 3px
		width: 30px
		background: #2c3e50
		transform: rotate(180deg)

		&::before
			content: ''
			position: absolute
			top: -9px
			height: 3px
			width: 30px
			background: #2c3e50			
			transition: all 200ms ease-out
		
		&::after
			content: ''
			position: absolute
			top: 9px
			height: 3px
			width: 30px
			background: #2c3e50
			transition: all 200ms ease-out
	&.open
		.bar
			&::before
				transform: rotateZ(45deg) scaleX(0.625) translate(14px, -4px)
				transition: all 200ms ease-in
			&::after
				transform: rotateZ(-45deg) scaleX(0.625) translate(14px, 4px)
				transition: all 200ms ease-in

main 
	position: relative
	@include breakpoint (medium)
		display: flex
		align-items: flex-start
		justify-content: space-between
		min-height: 100vh
		overflow-x: hidden

#desk 
	display: block
	margin: 0 auto
	width: 300px
	height: auto

#left,
#right
	display: block
	width: 100%

	@include breakpoint (medium)
		width: 50%
		height: 100%

#left
	padding: 2rem
	height: 70vh
	@include breakpoint (medium)
		position: fixed
		top: 0
		left: 0
		bottom: 0
	position: relative
	display: flex
	align-items: center
	justify-content: center
	height: 100%
	background: #fdfdfd
	font-weight: 300

	h1,
	h2,
	#social 
		text-align: center
	
	#social 
		padding: 0
		
		li 
			display: inline-block
			margin: 0 .5rem
			a 
				color: #2c3e50

#right 
	@include breakpoint (medium)
		position: absolute
		top: 0
		right: 0
	height: auto

// Fade transitions.
.fade-enter-active
	transition: all .3s ease-in

.fade-leave-active
	transition: all .5s ease-out

.fade-enter,
.fade-leave-to
	opacity: 0
	transform: translateX(10px)

// Default transitions
.v-enter 
	opacity: 0
	transition: all 500ms ease-out

.v-enter-to
	opacity: 1
	transition: all 500ms ease-in

.v-leave
	opacity: 1
	transition: all 500ms ease-in
.v-leave-to
	opacity: 0
	transition: all 500ms ease-out

</style>



























